import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiEntrenaService } from 'src/app/Services/Api/ApiEntrena.service';
import { StorageWebService } from 'src/app/Services/Storage/StorageWeb.service';
import { ToastService } from 'src/app/Services/Utilities/Toast.service';
import { AlertController } from '@ionic/angular';
import { DataComparnyService } from 'src/app/Services/dataCompany.service';
@Component({
  selector: 'app-designer-list',
  templateUrl: './designer-list.page.html',
  styleUrls: ['./designer-list.page.scss'],
})
export class DesignerListPage implements OnInit {

  listDesigner = [];
  text = '';
  color = '';

  constructor(
    private router: Router,
    private apiEntrena: ApiEntrenaService,
    private storage: StorageWebService,
    private toasCtrl: ToastService,
    private alertCtrl: AlertController,
    private dataCompany: DataComparnyService
    ) { 
    
      this.dataCompany.$color.subscribe((color) => {
          this.color = color;
      })

    }
  ngOnInit() {
  }

  ionViewWillEnter(){
   this.ListDesigners();

  }


  async ListDesigners() {
    const login = await this.storage.getLogin();

    if ( login ) {
        const id = login[0].data.role === 'USER_ENTRENA' ? login[0].data.parentAdmin : '';
        this.apiEntrena.readDesignersAll(login[0].token, id).subscribe((data: any) => {
          if (data.status === 'ok') {
            this.listDesigner = data.response;
          } else {
            this.toasCtrl.newCreatedToast('Could not create design, please try again later', false)
          }
        }, (error) => {
          this.toasCtrl.newCreatedToast('Could not create design, please try again later', false)
        })
    }
  }

  async newDesigner() {
    const login = await this.storage.getLogin();

    if ( login ) {
        this.apiEntrena.createDesigner(login[0].token).subscribe((data: any) => {
          if (data.status === 'ok') {
            this.router.navigate(['/designer', data.response._id]);
          } else {
            this.toasCtrl.newCreatedToast('Could not create design, please try again later', false)
          }
        }, (error) => {
          this.toasCtrl.newCreatedToast('Could not create design, please try again later', false)
        })
    }
  }


  async deleteDesign(design) {
    const alert = await this.alertCtrl.create({
      header: 'Esta seguro de eliminar este diseÃ±o ?',
      buttons: [
        {
          text: 'Cancelar',
          role: 'cancel'
        },
        {
          text: 'Aceptar',
          handler: async () => {
            const login = await this.storage.getLogin();

            if (login) {
              this.apiEntrena.deleteDesigner(login[0].token, design._id).subscribe((data: any) => {
                console.log(data, 'eliminando');
                if (data.status === 'ok') {
                  this.toasCtrl.newCreatedToast('Modulo eliminado de la lista', true);
                  this.ListDesigners();
                } else {
                  this.toasCtrl.newCreatedToast('No se pudo eliminar el modulo, intentelo mas tarde', false);
                }
              }, (error) => {
                this.toasCtrl.newCreatedToast('No se pudo eliminar el modulo, intentelo mas tarde', false);
              })
            }
          }
        }
      ]
    })

    await alert.present();
  }

  changeText( event ) {
    this.text = event.detail.value;
  }

}
